





































import { Component, Prop, Vue, Emit, Watch } from 'vue-property-decorator'
import { createComment, getComment, createReply } from '../../../api/comments'
import { UserModule }  from '../../../store/modules/user'

@Component
export default class extends Vue {
  @Prop() private type!: string
  @Prop() private data!: any
  private visible: boolean = false
  private firstVisible: boolean = true
  private placeholder: string = ''

  private showPopper: boolean = false
  private value: string = ''
  private emojis: string[] = ['ðŸ˜„', 'ðŸ˜˜', 'ðŸ˜‚', 'ðŸ˜³', 'ðŸ˜', 'ðŸ‘', 'ðŸ‘','ðŸ‘Ž','ðŸ˜','ðŸ˜‰','ðŸ˜ ','ðŸ˜ž',
  'ðŸ˜¥', 'ðŸ˜­','ðŸ˜','ðŸ˜¡','ðŸ’”','ðŸ˜£','ðŸ˜”','ðŸ˜„','ðŸ˜·','ðŸ˜š','ðŸ˜“','ðŸ˜Š','ðŸ˜¢','ðŸ˜œ','ðŸ˜¨','ðŸ˜°','ðŸ˜²','ðŸ˜',
  'ðŸ˜±','ðŸ˜ª','ðŸ˜–','ðŸ˜Œ','ðŸ˜’','ðŸ‘»','ðŸŽ…','ðŸ‘§','ðŸ‘¦','ðŸ‘©','ðŸ‘¨','ðŸ¶','ðŸ±','ðŸ‘Š','âœŠ','ðŸ’ª','ðŸ‘†','ðŸ‘‡','ðŸ‘‰','ðŸ‘ˆ',
  'ðŸ‘Œ','ðŸ’©']

  @Watch('type', {immediate: true})
  private typeChange(val: string) {
    this.showPopper = val === 'first' ? true : false
  }
  
  get disabled() {
    return this.value === '' ? true : false
  }
  get token() {
    return UserModule.token
  }

  @Emit('submit')
  private submit() {
    this.showPopper = false
    if (this.data) {
      Object.assign(this.data, {value: this.value})
      this.value = ''
      return this.data
    } else {
      this.showPopper = true
      this.firstVisible = false
      const result: string = this.value
      this.value = ''
      return result
    }
  }

  private handleFocus() {
    if (this.token) {
      this.visible = true
      this.firstVisible = true
    } else {
      UserModule.handleIslogin(true)
    }
  }

  private select(emoji: string) {
    this.value = `${this.value}${emoji}`
  }

  private showBtn() {
    this.visible = true
    this.placeholder = `å›žå¤${this.data.comment_author || this.data.nickname}...`
    document.addEventListener('click', this.hidePanel, false)
  }
  
  private hideBtn() {
    this.visible = false
    this.showPopper = this.type == 'first' ? true : false
    document.removeEventListener('click', this.hidePanel, false)
  }

  private hidePanel(e: any) {
    if (this.$refs.formBox && !(this.$refs.formBox as Element).contains(e.target)) {
      this.hideBtn()
    } else {
      this.showBtn()
    }
  }
 
  public created() {
    document.addEventListener("click", (e: any) => {
      this.hidePanel(e)
    })
  }

  public beforeDestroy() {
    this.hideBtn()
  }
}
